*{
    transition: 0.2s;
}

.Header{
    grid-area: Header;
    background: #112147;
    width: 100%;
    height: 60px;
    display:grid;
    grid-template-areas:
            'Logo HeaderMain Login ';
    grid-template-columns: 2fr 8.5fr 1.5fr ;
    margin: 0;
    font-family: 'Popins', sans-serif;
    padding: 0;
    z-index: 2;
    position: relative;


  }
.Header a:hover {

    color: #bf1650;
    /*#ec5990;*/


}

.logo{
    color: #ffffff;
    grid-area: Logo;
    position: relative;
    text-decoration: none;
    font-size: 2em;
    text-transform: uppercase;
    letter-spacing: 2px;
    margin-top: 10px;
    margin-left: 50px;


}
.logo a{

    color: #ffffff;
    text-decoration: none;

}
.HeaderMain{
    grid-area: HeaderMain;
    align-items: center;
    justify-content: space-between;
    text-align: right;

}
.HeaderMain a, .Login a{
    color: #ffffff;
    text-decoration: none;
    display: inline-block;
    letter-spacing: 2px;
    text-indent: 5em;
    font-size: 20px;
    margin-top: 20px;


}




