
.Content{
    /*width: 1920px;*/
    /*height: 1080px;*/
    margin: 0 auto;
    padding: 0;
    display:grid;
    grid-template-areas:
      'Header Header Header'
      'sidebar maincontent maincontent'
      'footer footer footer';
    grid-template-rows: 0.2fr 11fr 1fr;
    grid-template-columns: 0.5fr 11.5fr;


  }

  .Maincontent{
    grid-area: maincontent;
    background-color: #191d3a ;
      background-position: center;
      background-size: cover;
      height: 100%;
      width: 100%;
      /*z-index: 0;*/
  }