*{
  transition: 0.2s;
}


.sidebar {
  grid-area: sidebar;
  background: #2F323A;
  width: 20px;
  padding: 20px;
  font-size: 20px;
  font-family: 'Popins', sans-serif;
  overflow: hidden;
z-index: 0;
  position: fixed;
  height: 100%;
  padding-top: 80px;




}
.sidebar:hover {
 width: 160px;
  transition: 0.5s;

}
.sidebar a{
  overflow: hidden;
  color: #999999;
  text-decoration: none;
  display: block;
  line-height: 20px;
  width: 100%;
  text-decoration: none;
  margin-left: 0px;
  white-space: nowrap;





}
.sidebar a:hover{

  color: #fff;





}
.sidebarImg img {
  display: inline-block;
  width: 100px;
  height: 100px;
  border-radius: 50px;
  margin-bottom: 40px;
  margin-left: 20px;
  margin-top: 0px;

}

.sidebarImg h3{
  color: #999999;
  font-size: 20px;
  text-transform: uppercase;
  letter-spacing: 2px;
  margin-top: 0px;
  margin-bottom: 5px;
  margin-left: 40px;


}
