
.Form {
    background-color: #4f6294;
    width: 1000px;
    height: 1000px;
    margin: auto;
    border-radius: 15px;
    font-weight: 700;
    font-size: 24px;
    color: white;

}
.Form input{
    border-radius: 6px;
    line-height: 40px;

    left: 200px;
    margin: 60px 0 5px 0;
    width: 600px;
    color: black;


}
.Form button{
    position: relative;
    width: 300px;
    height: 60px;
    margin: 70px 0px 0px 350px;
    background-color: #ec5990;
    border-radius: 10px;
    color: white;
    letter-spacing: 5px;
    text-decoration: none;
    font-size: 24px;

}
